.benefits__list {
  text-align: center;
  column-gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.benefits__list-item {
  padding: 30px 0 0;
  transition: all .2s;
}

.benefits__icon-block {
  width: 90px;
  height: 90px;
  border: 1px solid var(--primary-color);
  text-align: center;
  border-radius: 50%;
  margin: 0 auto 74px;
  line-height: 90px;
  transition: all .2s ease-in-out;
}

.benefits__icon {
  vertical-align: middle;
  transition: all .2s ease-in-out;
}

.benefits__list-item:hover .benefits__icon-block {
  transform: scale(1.25);
}

.benefits__list-item:hover .benefits__icon {
  transform: scale(.9);
}

.benefits__list-item__description-text {
  margin-bottom: 10px;
}

